import React, { Component } from "react";
import Card from "react-md/lib/Cards/Card";
import CardTitle from "react-md/lib/Cards/CardTitle";
import Link from "gatsby-link";
import Media from "react-md/lib/Media";
import PostCover from "../PostCover/PostCover";
import "./PostPreview.scss";

class PostPreview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobile: true
    };
    this.handleResize = this.handleResize.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }
  componentDidMount() {
    this.handleResize();
    window.addEventListener("resize", this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  handleResize() {
    if (window.innerWidth >= 640) {
      this.setState({ mobile: false });
    } else {
      this.setState({ mobile: true });
    }
  }
  /* eslint class-methods-use-this: "off" */
  handleClick(e){
    if (e.target.parentNode.parentNode.classList.contains('preparing')) {
      e.preventDefault();
    };
  }
  render() {
    const { postInfo, fileEdges } = this.props;
    const { mobile } = this.state;
    const expand = mobile;
    /* eslint no-undef: "off" */
    const coverHeight = mobile ? 182 : 225;
    return (
      <Card
        key={postInfo.path}
        raise
        className="post-preview md-grid md-cell md-cell--12"
      >
        <Link style={{ textDecoration: "none" }} to={postInfo.path} onClick={this.handleClick}>
          <Media style={{ height: coverHeight, paddingBottom: "0px" }}>
            <PostCover
              fileEdges={fileEdges}
              postNode={postInfo}
              coverHeight={coverHeight}
              coverClassName={postInfo.lessonState}
            />
          </Media>
        </Link>
        <Link style={{ textDecoration: "none" }} to={postInfo.path}>
          <CardTitle expander={expand} title={postInfo.title} />
        </Link>
        <div className="badges">
          <span>{ postInfo.lessonPeriod }</span>
        </div>
        <span className="temp">{ postInfo.lessonState }</span>
      </Card>
    );
  }
}

export default PostPreview;



